const APIUtil = {
  followUser: id => {
    $.ajax({
      url: `/users/${id}/follow`,
      type: 'POST',
      dataType: 'json',
      });

  },

  unfollowUser: id => {
    $.ajax({
      url: `/users/${id}/follow`,
      type: 'DELETE',
      dataType: 'json',
      });
  }
};

module.exports = APIUtil;



